body {
  font-family: "Outfit", sans-serif;
  font-size: 1.6rem;
  background-color: #1a1b15;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.text-center {
  text-align: center;
}

h1, h2, h3 {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  color: #FFFFFF;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

[class$=__heading] {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  color: #FFFFFF;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

[class$=__descripcion] {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 300;
  color: #FFFFFF;
  margin-bottom: 5rem;
}

.header {
  min-height: 60rem;
  background-image: linear-gradient(to right, rgba(26, 27, 21, 0.5), rgba(26, 27, 21, 0.1)), image-set(url(../img/banner3.avif) 1x, url(../img/banner3.webp) 1x, url(../img/banner3.jpg) 1x);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.header__contenedor {
  width: min(95%, 140rem);
  margin: 0 auto;
}
.header__navegacion {
  padding: 5rem 0;
  display: flex;
  justify-content: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .header__navegacion {
    justify-content: flex-end;
  }
}
.header__enlace, .header__submit {
  all: unset;
  color: #FFFFFF;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
}
.header__enlace:hover, .header__submit:hover {
  color: #0da6f3;
  cursor: pointer;
}
.header__form {
  display: flex;
}
.header__contenido {
  padding-top: 10rem;
}
@media (min-width: 768px) {
  .header__contenido {
    padding-top: 5rem;
  }
}
.header__logo {
  font-size: clamp(2rem, -0.8rem + 10vw, 6rem);
  background: linear-gradient(to right, #dca100, #FFFFFF);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
}
@media (min-width: 768px) {
  .header__logo {
    text-align: left;
    display: inline-block;
  }
}
.header__texto {
  font-size: 3rem;
  font-weight: 700;
  color: #FFFFFF;
  margin: 2rem 0;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .header__texto {
    text-align: left;
  }
}
.header__texto--modalidad {
  font-size: 2rem;
}
.header__boton {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  text-transform: uppercase;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .header__boton {
    width: auto;
  }
}
.header__boton:hover {
  background-color: #0a85c3;
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__boton {
    width: auto;
  }
}

.barra {
  background-color: #000;
}
.barra__contenido {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .barra__contenido {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.barra__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  white-space: nowrap;
  font-size: clamp(2rem, -0.8rem + 10vw, 3rem);
}
@media (min-width: 1024px) {
  .barra__logo {
    text-align: left;
    margin: 0;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .navegacion {
    flex-direction: row;
  }
}
.navegacion__enlace {
  color: #FFFFFF;
  font-size: 2rem;
  padding: 2rem;
  font-weight: 900;
  text-transform: uppercase;
  text-align: center;
}
.navegacion__enlace--actual, .navegacion__enlace:hover {
  background-color: #dca100;
  color: #000;
}

.footer {
  background-color: #1a1b15;
  margin-top: 5rem;
  padding-top: 3rem;
  color: #FFFFFF;
  border-top: 5px solid #000;
}
.footer__grid {
  width: min(95%, 140rem);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.footer__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  white-space: nowrap;
  font-size: clamp(2rem, -0.8rem + 10vw, 4rem);
}
@media (min-width: 1024px) {
  .footer__logo {
    text-align: left;
    margin: 0;
  }
}
.footer__texto {
  line-height: 2;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__texto {
    text-align: left;
  }
}
.footer__copyright {
  background-color: black;
  margin: 3rem 0 0 0;
  padding: 2rem 0;
  text-align: center;
  font-size: 1.4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.footer__copyright--regular {
  font-weight: 400;
}

.menu-redes {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin: 2rem 0;
}
.menu-redes__enlace::before {
  color: #FFFFFF;
  font-size: 3rem;
  display: inline-block;
  font-style: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 6 Brands";
}
.menu-redes__enlace[href*="facebook.com/"]::before {
  content: "\f39e";
}
.menu-redes__enlace[href*="github.com"]::before {
  content: "\f09b";
}
.menu-redes__enlace[href*="tiktok.com"]::before {
  content: "\e07b";
}
.menu-redes__enlace[href*="instagram.com"]::before {
  content: "\f16d";
}
.menu-redes__enlace[href*="youtube.com"]::before {
  content: "\f167";
}
.menu-redes__enlace[href*="twitter.com"]::before {
  content: "\f099";
}
.menu-redes__ocultar {
  display: none;
}
@media (min-width: 768px) {
  .menu-redes {
    justify-content: flex-end;
  }
}

.formulario {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .formulario__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.formulario__fieldset {
  border: none;
  margin-top: 1rem;
  padding: 0;
}
.formulario__legend {
  color: #FFFFFF;
  font-size: 2.2rem;
  margin-bottom: 2rem;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1.5rem 0;
}
.formulario__contenedor-icono {
  display: flex;
  border-radius: 0.5rem;
  overflow: hidden;
}
.formulario__icono {
  background-color: #1E293B;
  width: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.formulario__icono i {
  color: #FFFFFF;
  font-size: 2rem;
}
.formulario__label {
  display: block;
  font-size: 1.8rem;
  color: rgba(255, 255, 255, 0.8);
}
.formulario__input, .formulario__input--sociales, .formulario__select--sociales, .formulario__select {
  padding: 1rem;
  border: 1px solid #b1c7dd;
  border-radius: 0.5rem;
  resize: none;
}
.formulario__input--file, .formulario__select--file {
  border: none;
  background-color: #FFFFFF;
}
.formulario__input--sociales, .formulario__select--sociales {
  flex: 1;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.formulario__input:disabled, .formulario__input--sociales:disabled, .formulario__select--sociales:disabled, .formulario__select:disabled {
  cursor: not-allowed;
}
.formulario__radio {
  display: flex;
  gap: 2rem;
}
.formulario__texto {
  margin-top: 3rem;
}
.formulario__imagen {
  max-width: 20rem;
}
.formulario__listado {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-top: 1.5rem;
}
.formulario__listado > li {
  background-color: #0da6f3;
  padding: 1rem;
  border-radius: 0.5rem;
  font-size: 1.4rem;
  color: #FFFFFF;
  font-weight: 700;
}
.formulario__submit {
  background-color: #dca100;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  align-self: flex-start;
}
@media (min-width: 768px) {
  .formulario__submit {
    width: auto;
  }
}
.formulario__submit:hover {
  background-color: #a97c00;
  cursor: pointer;
}
@media (min-width: 768px) {
  .formulario__submit {
    width: auto;
  }
}
.formulario__submit--registrar, .formulario__submit--full {
  width: 100%;
}
.formulario__descripcion {
  all: unset;
  color: #b1c7dd;
  font-style: italic;
}

.acciones, .acciones--centrar {
